
#desktop {
  display: block;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: -100;
  background: url("../../assets/bg-1.jpg") repeat right bottom transparent;

  background-size: 100%;
  overflow: hidden;
}




